import data from "./spisok.json";

function Filters() {
    return (
        <div classNameName="filters">
            <aside className="filter-menu">
                <h2></h2>

                <div className="filter-group">
                    <h3></h3>
                    <ul>
                    {data.map((item, index) => (
                            <li className="item_category" key={index}>
                                <a href="#" className="link_category">{item.category}</a>
                            </li>
                    ))}
                    </ul>
                </div>

                <div className="filter-group">
                    <h3></h3>
                    <ul>
                        {data.map((item,index) => (
                            <li className="item_brand" key={index}>
                            <a className="link_brand" href="#">{item.brand}</a>
                        </li>
                        ))}
                    </ul>
                </div>

                <div className="filter-group">
                    <h3></h3>
                    <div className="price-range">
                        <label for="min-price">:</label>
                        <input
                            type="number"
                            id="min-price"
                            min="0"
                            max="1000000000"
                        />
                        <label for="max-price">:</label>
                        <input
                            type="number"
                            id="max-price"
                            min="0"
                            max="1000000000"
                        />
                    </div>
                </div>

                <div className="filter-group">
                    <h3></h3>
                    <div className="rating">
                        <input
                            type="radio"
                            name="rating"
                            id="rating-4"
                            value="4"
                        />
                        <label for="rating-4">4   </label>
                        <br />
                        <input
                            type="radio"
                            name="rating"
                            id="rating-3"
                            value="3"
                        />
                        <label for="rating-3">3   </label>
                        <br />
                        <input
                            type="radio"
                            name="rating"
                            id="rating-2"
                            value="2"
                        />
                        <label for="rating-2">2   </label>
                        <br />
                        <input
                            type="radio"
                            name="rating"
                            id="rating-1"
                            value="1"
                        />
                        <label for="rating-1">1   </label>
                    </div>
                </div>

                <div className="filter-group">
                    <h3> </h3>
                    <div className="availability">
                        <input type="checkbox" id="in-stock" />
                        <label for="in-stock">  </label>
                    </div>
                </div>

                <button type="submit"></button>
            </aside>
        </div>
    );
}

export default Filters;
